@import url("https://cdn.jsdelivr.net/npm/@exampledev/new.css@1.1.2/new.min.css");

:root {
  --map-square-width: 25px;
  --map-border-color: var(--nc-bg-3);
  --map-hover-bg-color: var(--nc-bg-2);
  --wall-bg-color: var(--nc-bg-3);
  --visited-bg-color: var(--nc-bg-2);
  --frontier-border-color: #888;
  --solution-bg-color: var(--nc-bg-2);
  --solution-orb-bg-color: #99c6fa;
}

* {
  box-sizing: border-box;
}

body {
  max-width: none;
}

.Controls {
  margin-bottom: 2rem;
}

.Controls > * {
  margin-right: 0.5rem;
  margin-bottom: 0.5rem;
}

.Controls > *:last-child {
  margin-bottom: 0;
}

@media screen and (min-width: 600px) {
  .Controls > * {
    margin-bottom: 0;
  }
}

.PathMap {
  display: grid;
}

.PathMap > * {
  width: var(--map-square-width);
  height: var(--map-square-width);
  line-height: var(--map-square-width);
  text-align: center;
  cursor: pointer;
  user-select: none;
  border: 1px solid var(--map-border-color);
  transition: all 0.1s linear;
}

.PathMap > [class=""]:hover,
.PathMap > :not([class]):hover {
  background-color: var(--map-hover-bg-color);
}

.PathMap > .start {
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M15.75 9.525l-4.586-4.586a1.5 1.5 0 00-2.121 2.122l4.586 4.585a.5.5 0 010 .708l-4.586 4.585a1.5 1.5 0 002.121 2.122l4.586-4.586a3.505 3.505 0 000-4.95z"/></svg>');
}

.PathMap > .target {
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M12 4.5a7.5 7.5 0 107.5 7.5A7.508 7.508 0 0012 4.5zm0 12a4.5 4.5 0 114.5-4.5 4.505 4.505 0 01-4.5 4.5z"/></svg>');
}

@media (prefers-color-scheme: dark) {
  .PathMap > .start {
    background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path fill="%23eee" d="M15.75 9.525l-4.586-4.586a1.5 1.5 0 00-2.121 2.122l4.586 4.585a.5.5 0 010 .708l-4.586 4.585a1.5 1.5 0 002.121 2.122l4.586-4.586a3.505 3.505 0 000-4.95z"/></svg>');
  }

  .PathMap > .target {
    background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path fill="%23eee" d="M12 4.5a7.5 7.5 0 107.5 7.5A7.508 7.508 0 0012 4.5zm0 12a4.5 4.5 0 114.5-4.5 4.505 4.505 0 01-4.5 4.5z"/></svg>');
  }
}

.PathMap > .wall {
  background-color: var(--wall-bg-color);
}

.PathMap > .visited {
  background-color: var(--visited-bg-color);
  animation: visited 0.5s 1;
}

@keyframes visited {
  0% {
    border-color: var(--frontier-border-color);
  }

  100% {
    border-color: var(--map-border-color);
  }
}

.PathMap > .solution {
  background-color: var(--solution-bg-color);
  transition-duration: 0.25s;
  position: relative;
}

.PathMap > .solution:before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--solution-orb-bg-color);
  border-radius: 100%;
  transform: scale(0.4);
  animation: solution 0.5s 1;
}

@keyframes solution {
  0% {
    transform: scale(0);
  }

  50% {
    transform: scale(0.5);
  }

  100% {
    transform: scale(0.4);
  }
}
